.default {
    background-color: rgba(187, 86, 227, 0.91);
}
.default  {
    width: 130px;
    height: 40px;
    color: #fff;
    border-radius: 5px;
    padding: 10px 25px;
    font-family: 'Lato', sans-serif;
    font-weight: 500;
    background: transparent;
    cursor: pointer;
    transition: all 0.3s ease;
    position: relative;
    display: inline-block;
    box-shadow:inset 2px 2px 2px 0px rgba(255,255,255,.5),
    7px 7px 20px 0px rgba(0,0,0,.1),
    4px 4px 5px 0px rgba(0,0,0,.1);
    outline: none;
}
.default:disabled {
    background-color: darkgray;
}
.default:hover:not(:disabled) {
    background-color: #dc9fdb;
}

.red {
    background: darkred;
    width: 130px;
    height: 40px;
}
.red:hover {
    background: #d30101;
}

.buttons {
    text-align: center;
    letter-spacing: 1px;
    color: snow;
    margin: 15px;
    /*width: 70px;*/
    /*height: 30px;*/
    padding: 10px;
    border-radius: 30px;
    border: 1px solid #dc9fdb;
    font-size: 14px;
}

